.contacts {
  border: 1px solid;
  background-color: #141519;

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  .contactsContainer {
    display: flex;
    flex-direction: column;


    .inputForm {
      width: 50%;
      display: flex;
      flex-direction: column;
      gap: 15px;
      margin-bottom: 20px;


      .visitorsDate {
        font-size: 12px;
        color: #a0a8b3;
        font-weight: 500;
        letter-spacing: 1px;
        text-transform: uppercase;
        font-family: "Montserrat", sans-serif;
        margin-bottom: 10px;
        text-align: left;
        display: block;
      }

      .contactForm {
        background-color: #191b1e;
        border-radius: 6px;
        height: 55px;
        transition: 0.4s;
        border: 2px solid #191b1e;
        padding: 0 15px;
        font-size: 14px;
        color: #c4cfde;
        box-shadow: 1px 4px 2px -3px rgba(0, 0, 0, 0.7) inset, -1px -3px 3px -2px rgba(255, 255, 255, 0.2) inset;
        letter-spacing: 1px;
      }

      .btnSubmit {
        width: 50%;
        margin: 0 auto;
        padding: 10px 10px;
        background-color: #191b1e;
        border-radius: 6px;
        font-family: inherit;
        line-height: inherit;
        box-shadow: 5px 5px 5px #1c1e22;
        transition: 0.4s;
        position: relative;
        z-index: 2;
        font-size: 14px;
        font-weight: 500;
        border: 0 none;
        color: #FF014F;
        text-transform: uppercase;
      }
    }
  }
}


